import React from 'react'
import Header from './header'
import Footer from './footer'
import {window, document} from 'browser-monads'
import SimpleReactLightbox from 'simple-react-lightbox'

export default ({children}) => (
  <SimpleReactLightbox>
    <Header />
      {children}
    <Footer />
  </SimpleReactLightbox>
)
